<template>
  <div class="his-container">
    <div class="his-header clearfix">
      <div class="hd-left">
        <div style="margin-top: 40px">
          <el-row>
            <el-col :span="8">
              <span class="hd-flag" :style="{background: color[0]}">主版本发布</span>
            </el-col>
            <el-col :span="8">
              <span class="hd-flag" :style="{background: color[1]}">主版本回滚</span>
            </el-col>
            <el-col :span="8">
              <span class="hd-flag" :style="{background: color[2]}">灰度操作</span>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="hd-center">
        <div class="hd-title">发布历史</div>
        <div class="hd-desc">{{desc}}</div>
      </div>
      <div class="hd-right">
        <div class="hd-btn">
          <el-button type="primary" size="small" @click="goBack">返回项目首页</el-button>
        </div>
      </div>
    </div>
    <div class="his-main clearfix">
      <div class="main-left">
        <div class="main-left-in">
          <ul>
            <li @click="handleConfig(index,list)"
                :class="{'active': index === activated,'left-item':true}"
                v-for="(list,index) of historyList" :key="list.id">
              <div :class="{'item-style':true,
                'operation-normal': list.operation == 0  || list.operation == 5,
                'operation-gray': list.operation == 2 || list.operation == 3 ||  list.operation == 4 || list.operation == 7 || list.operation == 8,
                'operation-rollback': list.operation == 1 || list.operation == 6}">
                <el-row :gutter="5">
                  <el-col :span="8" class="text-overflow">{{list.operator}}</el-col>
                  <el-col :span="8" class="text-overflow">{{list.operation | operationType}}</el-col>
                  <el-col :span="8" class="text-overflow">{{list.releaseTimeFormatted}}</el-col>
                </el-row>
              </div>
            </li>
            <li v-if="addMoreFlag">
              <div class="more-style" @click="addMore">加载更多 <i class="el-icon-arrow-down"></i></div>
            </li>
            <li v-else>
              <div class="end-style">已经到底</div>
            </li>
          </ul>
        </div>
      </div>
      <div class="main-right">
        <div class="main-hd">
          <div class="hd-row">
            <div class="hd-text" style="position: absolute;left: 0;">{{releaseTitle}}</div>
            <div class="hd-text">{{releaseTime | formatDateTime}}</div>
          </div>
          <div class="hd-row">
            <el-radio-group v-model="radio" size="small">
              <el-radio-button label="变更的配置"></el-radio-button>
              <el-radio-button label="全部配置"></el-radio-button>
            </el-radio-group>
          </div>
        </div>
        <div class="main-change">
          <div class="main-title">{{radio}}</div>
          <div class="no-data" v-if="hisChanges===[]">无配置更改</div>
          <div v-if="hisChanges!=[] && radio==='变更的配置'">
            <el-table :data="hisChanges"
                      stripe
                      max-height="140"
                      key="changes-table"
                      style="width: 100%">
              <el-table-column
                label="Type">
                <template slot-scope="scope">
                  <span>{{scope.row.type | changeType}}</span>
                </template>
              </el-table-column>
              <el-table-column
                prop="entity.firstEntity.key"
                label="Key">
              </el-table-column>
              <el-table-column
                prop="entity.firstEntity.value"
                label="Old Value">
              </el-table-column>
              <el-table-column
                prop="entity.secondEntity.value"
                label="New Value">
              </el-table-column>
            </el-table>
          </div>
          <div v-if="hisChanges!=[] && radio==='全部配置'">
            <el-table :data="hisChanges"
                      stripe
                      max-height="140"
                      key="all-table"
                      style="width: 100%">
              <el-table-column
                prop="entity.firstEntity.key"
                label="Key">
              </el-table-column>
              <el-table-column
                prop="entity.secondEntity.value"
                label="Value">
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="main-gray">
          <div class="main-title">灰度规则</div>
          <div class="no-data" v-if="rules===[]">无灰度规则</div>
          <el-table v-else
                    :data="rules"
                    stripe
                    max-height="140"
                    key="rules-table"
                    style="width: 100%">
            <el-table-column
              prop="clientAppId"
              label="灰度的AppId">
            </el-table-column>
            <el-table-column
              label="灰度的IP">
              <template slot-scope="scope">
                <span>{{scope.row.clientIpList.join('，')}}</span>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        activated: 0,
        currentPage: 0,
        pageSize: 10,
        radio: '变更的配置',
        releaseTitle: '',
        releaseTime: '',
        rules: [],
        addMoreFlag: true,
        color: ['#316510', '#997f1c', '#999'],
        desc: ''
      }
    },
    computed: {
      ...mapState({
        historyList: state => state.releaseHis.historyList,
        hisChanges: state => state.releaseHis.hisChanges
      })
    },
    activated() {
      const vm = this
      vm.hisParams = vm.$route.query
      vm.desc = '(AppId：' + vm.hisParams.appId + '；ENV：' + vm.hisParams.env + '；Cluster：' + vm.hisParams.clusterName + '；NameSpace：' + vm.hisParams.namespaceName + ')'
      vm.getHisData()
    },
    methods: {
      ...mapActions('releaseHis', ['getReleaseHis', 'getCompared']),
      getHisData() {
        const vm = this
        vm.getReleaseHis(vm)
      },
      handleConfig(index, list) {
        const vm = this
        vm.activated = index
        vm.releaseTitle = list.releaseTitle
        vm.releaseTime = list.releaseTime
        vm.rules = list.operationContext.rules
        vm.env = vm.hisParams.env
        vm.compareParams = {
          baseReleaseId: list.previousReleaseId,
          toCompareReleaseId: list.releaseId
        }
        vm.getCompared(vm)
      },
      goBack() {
        const vm = this
        vm.currentPage = 0
        vm.$router.push({
          name: 'configDetail',
          query: {
            appId: vm.hisParams.appId
          }
        })
      },
      addMore() {
        const vm = this
        vm.currentPage++
        vm.getHisData()
      }
    }
  }
</script>

<style type="text/scss" lang="scss" scoped>
  @import '../../assets/common';

  .his-container {
    border: 2px solid #DCDCDC;
    font-size: 12px;
    .active {
      background: #eee;
    }
    .no-data {
      @include line-height(140px);
      text-align: center;
      font-size: $font16;
      font-weight: bolder;
      color: #DCDCDC;
    }
    .his-header {
      width: 100%;
      height: 80px;
      border-bottom: 2px solid #DCDCDC;
      .hd-left {
        float: left;
        width: 24%;
        text-align: center;
        .hd-flag {
          padding: 5px;
          color: #fff;
          border-radius: 3px;
        }
      }
      .hd-center {
        float: left;
        width: 60%;
        text-align: center;
        .hd-title {
          padding: 15px;
          font-size: 20px;
        }
        .hd-desc {
          font-size: $font14;
          padding: 5px;
        }
      }
      .hd-right {
        float: left;
        width: 16%;
        text-align: right;
        .hd-btn {
          margin-top: 35px;
          margin-right: 10px;
        }
      }
    }
    .his-main {
      width: 100%;
      height: 550px;
      overflow: hidden;
      .main-left {
        position: relative;
        float: left;
        width: 24%;
        height: 550px;
        overflow: hidden;
        .main-left-in {
          position: absolute;
          left: 0;
          top: 0;
          right: -17px;
          bottom: 0;
          overflow-y: scroll;
          .more-style {
            padding: 20px;
            text-align: center;
            cursor: pointer;
            font-size: $font16;
          }
          .more-style:hover {
            color: #409EFF;
          }
          .end-style {
            padding: 20px;
            text-align: center;
            font-size: $font16;
            color: #ccc;
          }
          .left-item {
            @include line-height(75px);
            border-bottom: 1px solid #DCDCDC;
            cursor: pointer;
            .item-style {
              margin-left: 10px;
              border-right: 8px solid;
              border-radius: 5px;
            }
            .operation-normal {
              border-right-color: #316510;
            }
            .operation-gray {
              border-right-color: #999;
            }
            .operation-rollback {
              border-right-color: #997f1c;
            }
          }
          .left-item:hover {
            background: #eee;
          }
        }
      }
      .main-right {
        float: left;
        width: 76%;
        height: 550px;
        margin: 0;
        border-left: 2px solid #DCDCDC;
        .main-hd {
          height: 100px;
          background: #eee;
          border-bottom: 1px solid #DCDCDC;
          .hd-row {
            position: relative;
            text-align: right;
            margin-right: 10px;
            .hd-text {
              display: inline-block;
              height: 50px;
              padding: 15px;
              font-size: $font16;
            }
          }
        }
        .main-change {
          height: 225px;
          padding: 20px;
          border-bottom: 1px solid #DCDCDC;
        }
        .main-gray {
          height: 225px;
          padding: 20px;
        }
        .main-title {
          height: 20px;
          padding-bottom: 20px;
          margin-bottom: 8px;
          font-size: $font16;
          font-weight: bolder
        }
      }
    }
  }
</style>



